.main-section {
    width: 85%;
    margin-left: auto;
    margin-right: auto;
    min-height: 600px;
}
#demo-button {
    position: absolute;
    top:0;
    left: 50%;
    z-index: 3;
    height: 50px;
    font-family: 'Lobster';
    width: 200px;
    margin-top: 50px;
    margin-left: -100px;
    font-size: 32px;
    color: rgb(65, 79, 71);
    border-radius: 0;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    cursor: pointer;
    border: 1px transparent;
    animation: hello 4s linear infinite;
}
@keyframes hello {
    0% {
        color: rgb(65, 79, 71);
        background: white;
    }
    50% {
        color: white;
        background: rgb(65, 79, 71);
        }
    100% {
        color: rgb(65, 79, 71);
        background: white;
    }
}
.main {
    display: flex;
    flex-direction: column;
    background: url('../images/Muir-Wood.jpg');
    background-attachment: fixed;
    background-size: cover;
    padding-bottom: 25px;
    padding-top: 75px;
    animation: fadeIn .75s;
}
@keyframes fadeIn {
    0% {
        opacity: 0.1
    }
    100% {
        opacity: 1
    }
}
.gm-svpc {
    visibility: hidden;
}
.gm-fullscreen-control {
    visibility: hidden;
}
.gm-style-mtc{
    visibility: hidden;
}
.loading-container {
    margin-top: 50px;
    text-align: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-left: auto;
    margin-right: auto;
    background: rgb(65, 79, 71,0.9);
    border: 5px solid rgb(65, 79, 71);
    border-top: 5px solid  white;
    animation: spinner 1s linear infinite;
  }
  #globe {
      font-size: 30px;
      margin-top: 10px;
      margin-bottom: 10px;
      color: white;
  }
  @keyframes spinner {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
@media only screen and (min-width: 520px ){
    #demo-button {
        width: 200px;
    }
}



